// for footer style

footer{
@extend %main-div;
min-height: 550px;
background-image: url('../image/con3.jpg');
&:hover{
.over{
@extend %over;
    clip-path: circle(1100px at 50% 100%);
opacity: 1;

}
//media
@media(max-width:767px){
clip-path: circle(1200px at 50% 100%);
}

 h5{
color: $h-color;
}
 
span{
color: $p-color;
}
}
.over{
@extend %over;
    clip-path: circle(50px at 50% 100%);
@include transition (.5s);
opacity: 0;

}
.container{
h3{
font-weight: bold;
position: relative;
text-transform: capitalize;
color: $alt-color;
padding: 70px 0 50px 0;
text-align: left;
}
.row{
.col-sm-12{
margin-bottom: 50px;
.main-content{
h5{
font-weight: bold;
text-transform: capitalize;
@include transition (.5s);

}
span{
display: block;
text-transform: capitalize;
margin-bottom: 10px;
@include transition (.8s);
}
button{
position: relative;
    width: 130px;
    height: 40px;
    border: 2px solid $alt-color;
    background-color: transparent;
    margin: 15px 0 10px 0;
    outline: none;
    cursor: pointer;

a{
 text-decoration: none;
color: $alt-color;
font-weight: bold;
font-size: .8rem;
text-transform: uppercase;
letter-spacing: 2px;
z-index: 5;
position: relative;
@include transition(.4s);

}
&:hover{
&:after{
width: 130px;
}
a{
color: $h-color;
}
}
&::after{
position: absolute;
content: "";
height: 40px;
width:0;
background-color: $alt-color;
top: -2px;
left: -2px;
@include transition(.4s);
}
}
a{
margin: 0 5px;
text-decoration: none;
img.social{
height: 40px;


}
}
img.png-img{
height: 100px;
margin: 5px 5px;
text-align: center;
}
.block{
display: block;

img.png-img{
display: inline-block;
}
}
}
.main-content.img{
text-align: center;
// media
@media (max-width:991px){
text-align: left;
}
}
}
}
}
}