//this is ready component for navber

nav{
top: 0;
position: fixed!important;
width: 100%;
z-index: 999;
// background-color: $second-color;
background-color: transparent;
border-bottom: 1px solid transparent;
.container{
a.navbar-brand{
img{
height: 60px;
}
text-transform: capitalize;
}
button.navbar-toggler{
outline: none;

span{
background-image: url('../image/menu1.png');
height: 40px;
outline: none;
cursor: pointer;
}
}
div.collapse{
ul{

li{
margin: 0 15px;
position: relative;
border-bottom: 2px solid transparent;
//media
@media (max-width:991px){
margin: 0;
max-width: 60px;
}
&::after{
position: absolute;
content: "";
width: 0px;
border-bottom: 2px solid $alt-color;
@include border-R(2px);
left: 0;
bottom: 0;
}
&:hover {
&:after{
width: 100%;
}
}
a{
font-size: 1.2rem;
position: relative;
 color: $h-color;
text-transform: capitalize;
// width: 60px;


}
}
}
}
}
}
//nav active links

.active-li{
border-bottom:2px solid #f67e7f!important ;
}