//for header style 

header {
  position: relative;
  text-align: center;
  background: linear-gradient(60deg, rgba($main-color, 1) 0%, rgba($alt-color, 1) 100%);
  color: white;
min-height: 100vh;
@media(max-width:991px){
//  min-height: 750px;
 }
@media(max-width:991px){
min-height: 120vh;
}

// .logo {
