// }
.inner-header {
  height: 65vh;
  width: 100%;
  margin: 0;
  padding: 0;
}
.container{
.row{
z-index: 99;
position: relative;
.col-md-12{
h1{
padding: 140px 0 30px 0;
font-size: 3rem;
text-transform: capitalize;
font-weight: bold;
letter-spacing: 2px;
color: $h-color;
text-align: left;

span{
color: $alt-color;

}
}
p{
text-transform: capitalize;
font-weight: bold;
letter-spacing: 1px;
margin-top: -20px;
color: $p-color;
text-align: left;
}
button{
position: relative;
    width: 130px;
    height: 40px;
    border: 2px solid $alt-color;
    background-color: transparent;
    margin: 15px 0 10px 0;
    outline: none;
    cursor: pointer;
float: left;

a{
 text-decoration: none;
color: $alt-color;
font-weight: bold;
font-size: .8rem;
text-transform: uppercase;
letter-spacing: 2px;
z-index: 5;
position: relative;
@include transition(.4s);

}
&:hover{
&:after{
width: 130px;
}
a{
color: $h-color;
}
}
&::after{
position: absolute;
content: "";
height: 40px;
width:0;
background-color: $alt-color;
top: -2px;
left: -2px;
@include transition(.4s);
}
}
// style for second col
.main-content{
background-color: $alt-color;
width: 380px;
margin-top: 170px;
margin-left: auto;
box-shadow: -15px 15px 0px #f39899, -30px 30px 0px #f1a7a7;
//media for medium screen size
@media(max-width:991px){
margin-top: 60px;
width: 450px;
margin-left: calc(50% - 225px);
}
//media for small screen size 
@media(max-width:767px){
margin-top: 80px;
width: 350px;
margin-left: calc(50% - 175px);
}
.content-2{
width: 90%;
margin: auto;
text-align: left;
padding: 22px 15px;
h3{
text-align: left;
font-weight: bold;
text-transform: capitalize;
padding: 15px 0 10px 0;
font-size: 1.2rem;
color: $h-color;


}
input{
display: block;
width: 100%;
height: 30px;
@include border-R(5px);
display:inline-block;
margin:0 0 15px 0;
background-color: $h-color;
border: 1px solid transparent;
outline: none;
padding: 5px;
text-transform: capitalize;

}
input[type = submit]{
width: 80px;
height: 35px;
background-color: $main-color;
color: $h-color;
cursor: pointer;
@include transition(.2s);
&:hover{
background-color: $second-color;
}
}
}
}
}
}
}
.flex {
  /*Flexbox for containers*/
  display: flex;
   justify-content: center;
  // align-items: center;
  // text-align: center;

}
.waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px;
  /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}
.content {
  position: absolute;
    height: 20vh;
    text-align: center;
    background-color: white;
    bottom: 0;
@media (max-width:767px) {
//  height: 60vh;
}
}
/* Animation */
.parallax>use {
  animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}
.parallax>use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax>use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax>use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax>use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
  .content {
    height: 30vh;
  }
  h1 {
    font-size: 24px;
  }
}
}