// this for rest all html page value 
*{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;  
    box-sizing: border-box;                                
    outline: none;
    padding: 0px;
    margin: 0px;

} 
body{
overflow: hidden;
font-family: 'nixie_oneregular';
background-color: #fff;
}

@import'./variables';      

@import'./nav';

nav{
ul{
li{
&::after{
@include transition(.2s);
}
}
}
}
// toggle class for nav links
.active-li{
border-bottom:2px solid #f67e7f ;
}
//this for add background to navbar when scrolling 
.scrollNow{
background-color: $second-color;
@include transition(.5s);
}
// start header elements style

@import'./header';

// start about block elements

.about{
@extend %main-div;
padding-top: 80px;
@media(max-width:991px){
padding-top: 250px;
 }
@media(max-width:767px){
// padding-top: 350px;
}
.container{
text-align: left;

h3{
font-weight: bold;
text-transform: capitalize;
color: $alt-color;
font-size: 2rem;


}
.row{
.col-lg-6{
overflow: hidden!important;
margin-top: 50px;
.right-image{
@media (max-width:767px){
height: 200px;
}
}
.main{
h4{
font-weight: bold;
text-transform: capitalize;
color: #444;


}
p{
font-size: 1.2rem;
color: #888;
}
span{
color: #888;
background-color: $main-color;
    padding: 9px 10px;
span{
color: $alt-color;
}
}
span.second{
text-transform: uppercase;
letter-spacing: 3px;
background-color: $main-color;
}

}
.main-image{
@extend %main-div;
background-image: url('../image/about-2.jpg');
height: 350px;
overflow: hidden;
.over{
 @extend %over;
position: absolute;
clip-path: circle(50px at 100% 100%);
@include transition(.7s);
overflow: hidden;

.contents{
width: 95%;
margin-left: 2.5%;
text-align: center;
h4{
margin-top: 20px;
text-transform: capitalize;
font-weight: bold;
background-color: rgba($alt-color, .6);
color: $p-color;
padding: 20px;

}
span{
margin-top: 15px;
display: block;
text-transform: capitalize;
background-color: rgba($alt-color, .4);
color: $p-color;
font-size: 1rem;
    letter-spacing: 1px;
padding: 30px;

}
}
&:hover{
// transform: scale(1.4);
cursor: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/9632/happy.png"), auto;

clip-path: circle(900px at 100% 100%);

}
}
.over-tow{
clip-path: circle(50px at 0% 0%);
background-image: url('../image/about-3.png');
background-size: cover;
@media(min-width:700px) and (max-width:991px) {
background-image: url('../image/small-screen.png');
}
}
}
}
.col-sm-12{
img{
height: 90px;
display: block;
margin: auto;
}
.social{
text-align: center;
margin: 10px auto;
a{
margin: 0 10px;
text-decoration: none;
img{
height: 40px;

display: inline-block;
}
}
}
}
}
}
}


/* start service section elements style */
section{
padding-top: 50px;
overflow: hidden;
.preview{

@extend %main-div;
min-height: 500px;
background-image: url('../image/preview.jpeg');
overflow: hidden;
.over{
@extend %over;

}
.container{
.row{
.col-lg-6{


.main{
position: relative;
h2{
text-transform: capitalize;
font-weight: bold;
padding:50px 0 50px 0;
color: $alt-color;
}
span{
display: block;
color: $h-color;
text-transform: capitalize;
font-size: 1rem;

}
button{
position: relative;
    width: 130px;
    height: 40px;
    border: 2px solid $alt-color;
    background-color: transparent;
    margin: 15px 0 10px 0;
    outline: none;
    cursor: pointer;

a{
 text-decoration: none;
color: $alt-color;
font-weight: bold;
font-size: .8rem;
text-transform: uppercase;
letter-spacing: 2px;
z-index: 5;
position: relative;
@include transition(.4s);

}
&:hover{
&:after{
width: 130px;
}
a{
color: $h-color;
}
}
&::after{
position: absolute;
content: "";
height: 40px;
width:0;
background-color: $alt-color;
top: -2px;
left: -2px;
@include transition(.4s);
}
}
}
//second row
.row{
.col-xl-6{
margin-top: 30px;
img{
height: 40px;
}
span{
    width: 83%;
    float: right;
    padding-left: 10px;
color: $p-color;
}
span.last{
margin-bottom: 50px;
}
}
}
}
// 
.col-lg-6.respon{
text-align: right;
//media
@media (max-width:991px){
text-align: center;
}

img{
height: 350px;
margin-top: 100px;
@media (max-width:991px){
margin: 10px auto;
}
@media (max-width:767px){
height: 250px;
}
}
}
}
}
}
