h5{
text-transform: capitalize;
font-weight: bold;
color: $h-color;


}
span{
text-transform: capitalize;
font-weight: bold;
color: $h-color;
font-size: .8rem;
}
}
}
}
}
img.animated{
   
    height: 100px;
    position: absolute;
top: 50%;
left: 50%;
    transform: translate(-50%, -50%); 
   opacity: 0;
@include transition (.5s);


}
&::before{
    position: absolute;
    content: "";
    top: 0;
    width: 0;
    height: 4px;
    background-color: $h-color;
    z-index: 2;
@include transition (.5s);
}
&:hover{
&::before{
width: 100%;
}
img.animated{
opacity: 1;
}
}
}
